.home {
  position: relative;
  font-weight: 500;
}
.vectorIcon {
  width: 12px;
  position: relative;
  height: 6px;
}
.homeParent,
.property1default {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.homeParent {
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.property1default {
  height: 50px;
  justify-content: center;
  padding: var(--padding-mini) var(--padding-xl);
  box-sizing: border-box;
  text-align: left;
  font-size: 14px;
  color: var(--color-white);
  font-family: var(--font-inter);
}
