@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-mid: 17px;
  --font-size-base: 16px;
  --font-size-mini: 15px;
  --font-size-xs: 12px;

  /* Colors */
  --color-white: #fff;
  --color-lightsteelblue-100: #c7d3ff;
  --color-lightsteelblue-200: #aeafcf;
  --color-darkslateblue: #374b8f;
  --color-lightskyblue-100: #aec0ff;

  /* Gaps */
  --gap-xl: 20px;
  --gap-3xs: 10px;
  --gap-8xs: 5px;

  /* Paddings */
  --padding-mini: 15px;
  --padding-xl: 20px;
}
